.header {
	position: fixed;
	width: 100%;
	top: 0;
	color: var(--color-dark);
	background-color: var(--color-bglight);
	box-shadow: 0 0 10px rgba(255,255,255,0.5);

	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: var(--color-bglight);
	}

	.lang__selector {
		font-size: 16px;
	}

	&__container {
		max-width: 1180px;
		margin: 0 auto;
		padding: 0 10px;
	}

	&__body {
		position: relative;
		z-index: 2;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		height: 40px;
		align-items: center;
	}

	&__burger {
		display: none;
	}
	&__menu {
		background-color: var(--color-bglight);
		height: 100%;
	}

	&__list {
		display: flex;

		.active-link {
			&:after {
				opacity: 1;
				transition: all 0.5s;
			}
		}

		li {
			list-style: none;
			margin: 0 25px 0 0;
			cursor: pointer;
			&:last-child {
				margin: 0;
			}
		}
	}

	&__link {
		// color: var(--color-dark);
		position: relative;
		text-transform: uppercase;
		font-size: 16px;
		text-decoration: none;

		&:hover {
			color: #50422f;
			transition: all 0.4s;
		}

		&:after {
			content: "";
			position: absolute;
			height: 2px;
			width: 100%;
			background-color: var(--color-dark);
			opacity: 0;
			bottom: -3px;
			left: 0;
			transition: all 0.4s;
		}
	}

	.lang {
		&__items {
			background-color: var(--color-bglight);
			color: var(--color-dark);
			border-color: var(--color-dark);
			border-radius: 3px;
			font-size: 16px;
		}

		&__items:focus {
			border-color: var(--color-dark);
			outline: none;
		}

		&__link {
			position: relative;
			text-transform: uppercase;
			font-size: 16px;
			text-decoration: none;
		}
	}

	@media (max-width: 767px) {
		& {
//			height: 100%;
			z-index: 5;

			&__list{
				display: inline-block;
				li {
					font-size: 18px;
					margin: 0 0 20px 20px;
					&:last-child {
						margin: 0 0 0 20px;
					}
				}
			}

			&__link:after {
				height: 0;
			}

			&__burger {
				display: block;
				position: relative;
				width: 30px;
				height: 20px;
				z-index: 10;
				cursor: pointer;
			}

			

			&__burger span {
				background-color: var(--color-dark);
				position: absolute;
				width: 100%;
				height: 2px;
				left: 0;
				top: 9px;
				transition: all 0.3s;
			}

			&__burger:before,
			&__burger:after {
				content: "";
				background-color: var(--color-dark);
				width: 100%;
				position: absolute;
				height: 2px;
				left: 0;
				transition: all 0.3s;
			}

			&__burger:before {
				// width: 50%;
				top: 0;
			}

			&__burger:after {
				bottom: 0;
				// width: 100%;
			}

			&__menu {
				position: fixed;
				top: -100%;
				left: 0;
				width: 100%;
				height: 100%;
				transition: all 0.3s ease 0s;
				padding-top: 60px;
				//display: none;
			}
		}

		.lang {
			transform: scale(0);
			transition: all 0.3s ease 0s;
		}
		.menu__active:before {
			transform: rotate(45deg);
			top: 9px;

		}
		.menu__active:after {
			transform: rotate(-45deg);
			bottom: 9px;
		}
		.menu__active span {
			transform: scale(0);
		}

		.header__menu.menu__active {
			top: 0;
		}
	}
	.lang.menu__active {
		transform: scale(1);
	}

}
