html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  color: var(--color-black);
  background-color: var(--color-hover);
  overflow: hidden;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 15px;
  max-width: 1200px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  position: fixed;
  width: 100%;
  top: 0;
  color: var(--color-dark);
  background-color: var(--color-bglight);
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

.header:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-bglight);
}

.header .lang__selector {
  font-size: 16px;
}

.header__container {
  max-width: 1180px;
  margin: 0 auto;
  padding: 0 10px;
}

.header__body {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 40px;
  align-items: center;
}

.header__burger {
  display: none;
}

.header__menu {
  background-color: var(--color-bglight);
  height: 100%;
}

.header__list {
  display: flex;
}

.header__list .active-link:after {
  opacity: 1;
  transition: all 0.5s;
}

.header__list li {
  list-style: none;
  margin: 0 25px 0 0;
  cursor: pointer;
}

.header__list li:last-child {
  margin: 0;
}

.header__link {
  position: relative;
  text-transform: uppercase;
  font-size: 16px;
  text-decoration: none;
}

.header__link:hover {
  color: #50422f;
  transition: all 0.4s;
}

.header__link:after {
  content: "";
  position: absolute;
  height: 2px;
  width: 100%;
  background-color: var(--color-dark);
  opacity: 0;
  bottom: -3px;
  left: 0;
  transition: all 0.4s;
}

.header .lang__items {
  background-color: var(--color-bglight);
  color: var(--color-dark);
  border-color: var(--color-dark);
  border-radius: 3px;
  font-size: 16px;
}

.header .lang__items:focus {
  border-color: var(--color-dark);
  outline: none;
}

.header .lang__link {
  position: relative;
  text-transform: uppercase;
  font-size: 16px;
  text-decoration: none;
}

@media (max-width: 767px) {
  .header {
    z-index: 5;
  }
  .header__list {
    display: inline-block;
  }
  .header__list li {
    font-size: 18px;
    margin: 0 0 20px 20px;
  }
  .header__list li:last-child {
    margin: 0 0 0 20px;
  }
  .header__link:after {
    height: 0;
  }
  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    z-index: 10;
    cursor: pointer;
  }
  .header__burger span {
    background-color: var(--color-dark);
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    top: 9px;
    transition: all 0.3s;
  }
  .header__burger:before, .header__burger:after {
    content: "";
    background-color: var(--color-dark);
    width: 100%;
    position: absolute;
    height: 2px;
    left: 0;
    transition: all 0.3s;
  }
  .header__burger:before {
    top: 0;
  }
  .header__burger:after {
    bottom: 0;
  }
  .header__menu {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    transition: all 0.3s ease 0s;
    padding-top: 60px;
  }
  .header .lang {
    transform: scale(0);
    transition: all 0.3s ease 0s;
  }
  .header .menu__active:before {
    transform: rotate(45deg);
    top: 9px;
  }
  .header .menu__active:after {
    transform: rotate(-45deg);
    bottom: 9px;
  }
  .header .menu__active span {
    transform: scale(0);
  }
  .header .header__menu.menu__active {
    top: 0;
  }
}

.header .lang.menu__active {
  transform: scale(1);
}

.container {
  height: 100vh;
  padding-top: 0;
}

::-webkit-scrollbar {
  width: 0;
}

.flip-book {
  display: none;
  background-size: cover;
  margin-top: 60px;
}

.page {
  padding: 20px;
  background-color: #fdfaf7;
  color: #785e3a;
  border: solid 1px #c2b5a3;
}

.page .page-content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
}

.page .page-content .page-header {
  height: 30px;
  font-size: 100%;
  text-transform: uppercase;
  text-align: center;
}

.page .page-content .page-image {
  height: 100%;
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
}

.page .page-content .page-text {
  height: 100%;
  flex-grow: 1;
  font-size: 80%;
  text-align: justify;
  margin-top: 10px;
  padding-top: 10px;
  padding-right: 10px;
  box-sizing: border-box;
  border-top: solid 1px #f4e8d7;
  overflow-y: auto;
}

.page .page-content .page-footer {
  height: 30px;
  border-top: solid 1px #f4e8d7;
  font-size: 80%;
  color: #998466;
  padding-top: 15px;
}

.page.--left {
  border-right: 0;
  box-shadow: inset -7px 0 30px -7px rgba(0, 0, 0, 0.4);
}

.page.--right {
  border-left: 0;
  box-shadow: inset 7px 0 30px -7px rgba(0, 0, 0, 0.4);
}

.page.--right .page-footer {
  text-align: right;
}

.page.hard {
  background-color: #f2e8d9;
  border: solid 1px #998466;
}

.page.page-cover {
  background-color: #e3d0b5;
  color: #785e3a;
  border: solid 1px #998466;
}

.page.page-cover h2 {
  text-align: center;
  padding-top: 50%;
  font-size: 210%;
}

.page.page-cover.page-cover-top {
  box-shadow: inset 0px 0 30px 0px rgba(36, 10, 3, 0.5), -2px 0 5px 2px rgba(0, 0, 0, 0.4);
}

.page.page-cover.page-cover-bottom {
  box-shadow: inset 0px 0 30px 0px rgba(36, 10, 3, 0.5), 10px 0 8px 0px rgba(0, 0, 0, 0.4);
}

::-webkit-scrollbar {
  width: 16px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 4px grey;
  border-radius: 8px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--color-bglight);
  border-radius: 8px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--color-bglight);
}

.navcontainer {
  display: none;
}
